import { useParams, Link } from 'react-router-dom';
import { Fade } from 'react-awesome-reveal';

const demoEvents = [
  {
    id: 1,
    title: "  ",
    date: "2025-09-01",
    category: "",
    description:
      "          .   9:00   .",
  },
  {
    id: 2,
    title: "  ",
    date: "2025-09-15",
    category: "",
    description:
      "  .  :  ,    .   12:00.",
  },
  {
    id: 3,
    title: "  (1-4 )",
    date: "2025-09-20",
    category: "",
    description:
      "            .   17:30   .",
  },
  {
    id: 4,
    title: " ",
    date: "2025-10-01",
    category: "",
    description:
      " ,   ,   .   14:00.",
  },
];

function EventDetail() {
  const { id } = useParams();
  const event = demoEvents.find((e) => e.id === Number(id));

  if (!event) {
    return (
      <div className="container mt-4">
        <h2>  </h2>
        <Link to="/events" className="btn btn-secondary mt-3">
            
        </Link>
      </div>
    );
  }

  return (
    <div className="container mt-5">
      <Fade>
        <Link to="/events" className="btn btn-outline-secondary mb-3">
          &larr;   
        </Link>

        <div className="bg-light p-4 rounded shadow-sm">
          <h2>{event.title}</h2>
          <p className="text-muted mb-2">
            : {new Date(event.date).toLocaleDateString("uk-UA")} | :{' '}
            <span className={`badge bg-${getCategoryColor(event.category)}`}>
              {event.category}
            </span>
          </p>
          <hr />
          <p>{event.description}</p>
        </div>
      </Fade>
    </div>
  );
}

function getCategoryColor(category) {
  switch (category) {
    case "":
      return "success";
    case "":
      return "warning";
    case "":
      return "info";
    default:
      return "secondary";
  }
}

export default EventDetail;
