import { useRef, useEffect, useState } from "react";
import emailjs from "emailjs-com";
import axios from "axios";

function Contacts() {
  const form = useRef();
  const [ip, setIp] = useState("");

  useEffect(() => {
    axios.get("https://api.ipify.org?format=json")
      .then(res => setIp(res.data.ip))
      .catch(() => setIp("IP "));
  }, []);

  const sendEmail = (e) => {
    e.preventDefault();

    const timestamp = new Date().toLocaleString();
    form.current.querySelector('[name="timestamp"]').value = timestamp;

    form.current.querySelector('[name="user_ip"]').value = ip;

    emailjs.sendForm(
      'service_wchnpwc',       
      'template_pcwonro',     
      form.current,
      'XOs1foabTAX2M3Dzf'       
    )
    .then(() => {
      alert('  !');
      form.current.reset();
    }, () => {
      alert('    .');
    });
  };

  return (
    <div className="container mt-4">
      <h2></h2>
      <p> : . , 1, . ,  ,  </p>
      <p> : +380 44 123 45 67</p>
      <p> Email: school@example.com</p>

      <h4 className="mt-4"> '</h4>
      <form ref={form} onSubmit={sendEmail}>
        <div className="mb-3">
          <label className="form-label"></label>
          <input name="name" type="text" className="form-control" placeholder=" " required />
        </div>
        <div className="mb-3">
          <label className="form-label">Email</label>
          <input name="email" type="email" className="form-control" placeholder="example@gmail.com" required />
        </div>
        <div className="mb-3">
          <label className="form-label"> </label>
          <input name="subject" type="text" className="form-control" placeholder=":   " required />
        </div>
        <div className="mb-3">
          <label className="form-label"></label>
          <textarea name="message" className="form-control" rows="4" placeholder="  ..." required></textarea>
        </div>

        <input type="hidden" name="timestamp" />
        <input type="hidden" name="user_ip" />

        <button type="submit" className="btn btn-primary"></button>
      </form>
    </div>
  );
}

export default Contacts;
