import { Link } from 'react-router-dom';
import { useState } from 'react';
import { Fade } from 'react-awesome-reveal';

const demoNews = [
  {
    id: 1,
    title: "    !",
    content:
      "    ,    ,       .",
    date: "2025-09-01",
    category: "",
  },
  {
    id: 2,
    title: "   ",
    content:
      "        ,   .",
    date: "2025-09-15",
    category: "",
  },
  {
    id: 3,
    title: "      ",
    content:
      "         ,    .",
    date: "2025-10-05",
    category: "",
  },
];

function News() {
  const [selectedCategory, setSelectedCategory] = useState("");
  const [searchTerm, setSearchTerm] = useState("");

  const categories = ["", "", "", ""];

  const filteredNews = demoNews.filter((item) => {
    const matchesCategory =
      selectedCategory === "" || item.category === selectedCategory;
    const matchesSearch =
      item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.content.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesCategory && matchesSearch;
  });

  return (
    <div className="container mt-4">
      <h2 className="mb-4 text-center"> </h2>

      <div className="row mb-4">
        <div className="col-md-6 mb-2">
          <input
            type="text"
            className="form-control"
            placeholder=" ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        <div className="col-md-6 d-flex justify-content-md-end">
          <select
            className="form-select w-auto"
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
          >
            {categories.map((cat) => (
              <option key={cat} value={cat}>
                {cat}
              </option>
            ))}
          </select>
        </div>
      </div>

      {filteredNews.length === 0 ? (
        <p className="text-center fst-italic">  .</p>
      ) : (
        <Fade cascade damping={0.1}>
          {filteredNews.map(({ id, title, content, date, category }) => (
            <div className="card mb-3 shadow-sm" key={id}>
              <div className="card-body">
                <h5 className="card-title">{title}</h5>
                <small className="text-muted">
                  : {new Date(date).toLocaleDateString("uk-UA")} | :{" "}
                  <span className="badge bg-info text-dark">{category}</span>
                </small>
                <p className="card-text mt-2">{content}</p>
                <Link
                  to={`/news/${id}`}
                  className="btn btn-sm btn-outline-primary"
                >
                  
                </Link>
              </div>
            </div>
          ))}
        </Fade>
      )}

      <div className="mt-5 p-4 bg-light rounded shadow-sm text-center">
        <h4>   </h4>
        <p>      email.</p>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            alert("  !");
          }}
          className="d-flex justify-content-center"
        >
          <input
            type="email"
            placeholder=" email"
            required
            className="form-control w-50 me-2"
          />
          <button type="submit" className="btn btn-primary">
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default News;
