import { Link } from "react-router-dom";

function Navbar({ darkMode, setDarkMode }) {
  return (
    <nav className={`navbar navbar-expand-lg ${darkMode ? "navbar-dark bg-dark-theme" : "navbar-dark bg-primary"}`}>
      <div className="container">
        <Link className="navbar-brand" to="/"> </Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav me-auto">
            <li className="nav-item"><Link className="nav-link" to="/about"> </Link></li>
            <li className="nav-item"><Link className="nav-link" to="/news"></Link></li>
            <li className="nav-item"><Link className="nav-link" to="/teachers"></Link></li>
            <li className="nav-item"><Link className="nav-link" to="/contacts"></Link></li>
            <li className="nav-item"><Link className="nav-link" to="/testimonials"></Link></li>
            <li className="nav-item"><Link className="nav-link" to="/gallery"></Link></li>
            <li className="nav-item"><Link className="nav-link" to="/eventscalendar"></Link></li>
          </ul>
          <button
            className={`btn btn-sm ${darkMode ? "btn-outline-light" : "btn-outline-light"} ms-auto`}
            onClick={() => setDarkMode(!darkMode)}
          >
            {darkMode ? " " : " "}
          </button>
        </div>
      </div>
    </nav>
  );
}


export default Navbar;
