import { useParams, Link } from 'react-router-dom';
import { Fade } from 'react-awesome-reveal';

const demoNews = [
  {
    id: 1,
    title: "    !",
    content: `
              .    ,            .       ,     .        .
    `,
    date: "2025-09-01",
    category: "",
    image: "https://via.placeholder.com/800x400?text=1+",
    quote: "      !  ,  5-",
    video: "https://www.w3schools.com/html/mov_bbb.mp4",
  },
  {
    id: 2,
    title: "   ",
    content: `
             ,      , ,     .        .
    `,
    date: "2025-09-15",
    category: "",
    image: "https://via.placeholder.com/800x400?text=",
    quote: "      !  ,  7-",
    video: "",
  },
  {
    id: 3,
    title: "      ",
    content: `
               .      !          .
    `,
    date: "2025-10-05",
    category: "",
    image: "https://via.placeholder.com/800x400?text=",
    quote: "       !  , 9-",
    video: "",
  },
];

function NewsDetail() {
  const { id } = useParams();
  const newsItem = demoNews.find((item) => item.id === Number(id));

  if (!newsItem) {
    return (
      <div className="container mt-4">
        <h2>  </h2>
        <Link to="/news" className="btn btn-secondary mt-3">
            
        </Link>
      </div>
    );
  }

  return (
    <div className="container mt-4">
      <Link to="/news" className="btn btn-outline-secondary mb-3">
        &larr;   
      </Link>

      <Fade cascade>
        <h2>{newsItem.title}</h2>
        <small className="text-muted">
          : {new Date(newsItem.date).toLocaleDateString("uk-UA")} | :{" "}
          <span className="badge bg-info text-dark">{newsItem.category}</span>
        </small>

        <hr />

        <img
          src={newsItem.image}
          alt=" "
          className="img-fluid rounded mb-3 shadow-sm"
        />

        <p style={{ whiteSpace: 'pre-line' }}>{newsItem.content}</p>

        {newsItem.video && (
          <div className="my-4">
            <h5>  </h5>
            <video controls className="w-100 rounded shadow">
              <source src={newsItem.video} type="video/mp4" />
                  .
            </video>
          </div>
        )}

        {newsItem.quote && (
          <blockquote className="blockquote mt-4 p-3 bg-light rounded shadow-sm">
            <p className="mb-0 fst-italic">{newsItem.quote}</p>
          </blockquote>
        )}
      </Fade>
    </div>
  );
}

export default NewsDetail;
