import { useParams, Link } from "react-router-dom";
import teacher_1 from '../assets/teacher_1.jpg';
import teacher_2 from '../assets/teacher_2.jpg';
import teacher_3 from '../assets/teacher_3.jpg';

const teachers = [
  { id: "ivanov", name: " ", subject: "", photo: teacher_1, description: "    15- .", bio: "       2005   .         ." },
  { id: "petrova", name: " ", subject: " ", photo: teacher_2, description: "     .", bio: "               ." },
  { id: "sergiy", name: " ", subject: "", photo: teacher_3, description: " ,    .", bio: "       ,   ." },
];

function TeacherDetail() {
  const { id } = useParams();
  const teacher = teachers.find(t => t.id === id);

  if (!teacher) {
    return (
      <div className="container mt-4">
        <h2>  </h2>
        <Link to="/teachers" className="btn btn-primary mt-3">  </Link>
      </div>
    );
  }

  return (
    <div className="container mt-4">
      <Link to="/teachers" className="btn btn-outline-secondary mb-3">&larr; </Link>
      <div className="card mb-4 shadow">
        <div className="row g-0">
          <div className="col-md-4">
            <img src={teacher.photo} alt={teacher.name} className="img-fluid rounded-start" />
          </div>
          <div className="col-md-8">
            <div className="card-body">
              <h2 className="card-title">{teacher.name}</h2>
              <h5 className="text-primary">{teacher.subject}</h5>
              <p className="card-text">{teacher.description}</p>
              <hr />
              <h6></h6>
              <p>{teacher.bio}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeacherDetail;
